.rating-buttons {
  display: flex;
  justify-content: space-between;
  width: 260px;
  .rating {
    display: flex;
    align-items: flex-end;
    font-size: 25px;
    line-height: 0.75;
    transition: opacity 0.3s ease 0s;

    &.rating_sending {
      opacity: 0.2;
    }

    // .rating__body
    &__body {
      position: relative;
      cursor: pointer;
      &::before {
        content: "\e83a \e83a \e83a \e83a \e83a";
        font-family: "Material Icons";
        color: $purple;
        letter-spacing: -1px;
        display: block;
      }
    }

    // .rating__active
    &__active {
      position: absolute;
      width: 0%;
      height: 100%;
      top: 0;
      left: 0;
      overflow: hidden;
      &::before {
        content: "\e838 \e838 \e838 \e838 \e838";
        font-family: "Material Icons";
        letter-spacing: -1px;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        @include primary-gradient;
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    // .rating__items
    &__items {
      display: flex;
      position: absolute;
      width: 100%;
      height: 100%;
      top: -3px;
      left: 0;
    }

    // .rating__item
    &__item {
      flex: 0 0 16px;
      height: 100%;
      opacity: 0;
      cursor: pointer;
    }

    // .rating__value
    &__value {
      display: none;
    }
  }
}
